h1, h2, h3, h4, h5, h6, p {
  margin-top: .5em;
  margin-bottom: .5em;
}

ul {
  margin-top: .5em;
  margin-bottom: .5em;
  padding-left: 35px;
}

a {
  &:link {
    color: var(--primary);
    text-decoration: none;
  }

  &:visited {
    color: var(--primary);
  }

  &:hover {
    color: var(--primary-light);
  }

  &:active {
    color: var(--primary-dark);
  }
}

hr {
  height: 2px;
  background: linear-gradient(90deg, var(--gradient-start), var(--gradient-middle), var(--gradient-end));
  border: none;
}

.comment {
  &:before {
    content: '|| ';
    font-style: italic;
    color: var(--primary);
    opacity: .6;
  }
}
